import "../../../index.css";
import { useState } from "react";
import Swal from "sweetalert2";
import { Link } from "react-router-dom";
import { useDispatch } from "react-redux";
import { loginUser } from "../../../_actions/user_actions";

const Login = (props: any): JSX.Element => {
  const dispatch: any = useDispatch();
  const [account, setAccount] = useState("");
  const [password, setPassword] = useState("");
  const [loginSuccess, setloginSuccess] = useState(null);

  const accountChangeHandler = (evevt: any) => {
    setAccount(evevt.target.value);
  };

  const passwordChangeHandler = (evevt: any) => {
    setPassword(evevt.target.value);
  };

  const formHandler = async (evevt: any) => {
    evevt.preventDefault();
    let userData = {
      account,
      password,
    };

    dispatch(loginUser(userData)).then((res) => {
      const {
        payload: { loginSuccess },
      } = res;
      if (loginSuccess) {
        const userName = res.payload.userName;
        setloginSuccess(true);
        Swal.fire({
          icon: "success",
          title: `${userName} .`,
          showConfirmButton: false,
          timer: 1500,
        });
        setTimeout(() => {
          props.history.push("/admin/worship-management");
        }, 1000);
      } else {
        setloginSuccess(false);
        Swal.fire({
          icon: "error",
          title: res.payload.message,
          showConfirmButton: false,
          timer: 1500,
        });
      }
      //     input .
      setAccount("");
      setPassword("");
    });
  };

  return (
    <div className="full-screen bg-[#f0f0f0] flex justify-center items-center">
      <div className="py-14 flex justify-center">
        {loginSuccess && <></>}
        {!loginSuccess && (
          <>
            <form
              className="bg-white max-w-xs shadow-md rounded px-8 pt-8 pb-8 mb-4"
              onSubmit={formHandler}
            >
              <h2 className="pb-6 text-center text-[#35C5F0] font-extrabold">
                 
              </h2>
              <div className="mb-4">
                <input
                  className="shadow appearance-none border rounded w-full py-2 px-3 text-gray-700 leading-tight focus:outline-none focus:shadow-outline"
                  id="account"
                  type="text"
                  placeholder=""
                  value={account}
                  onChange={accountChangeHandler}
                />
              </div>
              <div className="mb-2">
                <input
                  className="shadow appearance-none border rounded w-full py-2 px-3 text-gray-700 mb-3 leading-tight focus:outline-none focus:shadow-outline"
                  id="password"
                  type="password"
                  autoComplete="on"
                  placeholder=""
                  value={password}
                  onChange={passwordChangeHandler}
                />
              </div>
              <button
                className="block w-full bg-[#35C5F0] hover:bg-[#44bee3] text-white font-bold py-2 px-4 rounded focus:outline-none focus:shadow-outline mb-6"
                type="submit"
              >
                
              </button>
              <div className="flex items-center justify-around	">
                <Link
                  className="inline-block align-baseline font-bold text-sm text-[#999] hover:text-[#777]"
                  to="/user/password"
                >
                   
                </Link>
                <Link
                  className="inline-block align-baseline font-bold text-sm text-[#999] hover:text-[#777]"
                  to="/sign-up"
                >
                  
                </Link>
              </div>
            </form>
          </>
        )}
      </div>
    </div>
  );
};

export default Login;
